.filter{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 10px;
}


@media screen and (min-width: 768px) {
  .filter{
    flex-direction: row;
  }
}

.filter__label {
  display:block;
  width:170px;
  border:2px solid white;
  border-color: #E0DEDC;
  border-radius:20px;
  text-align:center;
  padding:7px ;
  font-size:15px;
}

.filter__checkbox {
  position:absolute;
  right:10px;
  top:15px;
  width: 2.2em;
  height: 2.2em;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  border:none;
  padding:0;
  border-radius:0;
  vertical-align: middle;
  background-repeat:no-repeat;
  background-size:cover;
  background-color:transparent;
  transition:.3s ease;
  outline:0;
}

.checked{
    background-color: black;
    border-color: black;
    color: white;
}